.header-container {
  padding: 30px;
  padding-top: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.header-container a {
  text-decoration: none;
}

.header-logo {
  font-size: 40px;
  font-weight: bold;
  padding-left: 20px;
  font-family: var(--logo-font);
  display: flex;
  align-items: center;
}

.header-logo-text {
  display: inline-block;
  position: relative;
  color: #222;
}

.header-logo-img {
  width: 40px;
  height: 40px;
  image-rendering: pixelated;
}
